:root {
  --focus-color: white;
  --second-color: rgba(245, 245, 245, 0.8);
}
body{
  margin:0;
  padding: 0;
  overflow: hidden;
}
span.presentation {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: flex-start;
  padding: 0 100px;
  z-index: 100;
  }
div.conteiner {
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
div.conteiner * {
  margin-top: 0;
}
div.conteiner h1.title {
  color: var(--focus-color);
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 80px;
  margin: 0;
}
div.conteiner p.subtitle {
  color: var(--second-color);

  font-size: 0.8rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
div.conteiner p.description {
  color: var(--second-color);
  font-size: 1.1rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
div.btn-conteiner {
  display: flex;
  width: 35%;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}
div.btn-conteiner p.description {
  margin: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande";
  color: var(--focus-color);

}

div.btn-begin {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  outline: solid white 1px;
}
div.btn-begin:hover {
  background-color: rgba(245, 245, 245, 0.074);
}
.dot{
  display:  flex;
  align-items:  center;
  flex-direction: column;
  transition: all;
  z-index: 100;
  pointer-events: all;
} 

.dot img {
  border: solid rgb(119, 46, 255);
  border-radius: 50%;
  padding: 5px;
  width: 35px;
  height: 35px;
}
.dot h3, .dot h4{
  color: white;
  margin: 0;
  text-align: center;
}

.slide-in-left {
  visibility: visible !important;
	-webkit-animation: slide-in-left 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	        animation: slide-in-left 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

